import React from 'react'
import { NavLink } from "react-router-dom"

const Bar = (props) => {
  return (
    <div className="bar hidden-xs">
      <ul>
        {props.barList.map((i, index) => {return <li key={index}><NavLink to={i.url} activeClassName="active" exact>{i.title}</NavLink></li>;})}
      </ul>
    </div>
  )
}

export default Bar
